@tailwind base;
@tailwind components;
@tailwind utilities;



@layer base {
    body {
        @apply bg-gradient-to-r from-slate-600 to-blue-500;
    }
    h2 {
      @apply font-serif;
      @apply font-bold;
      @apply text-2xl;
      @apply mb-1;
    }
    p {
        @apply font-mono;
    }
    /* ... */
  }